<template>
  <div class=" mt-28 lg:px-20 px-8">
      <div v-if="ready" class=" border-b pb-6">
          <h3 class=" font-mono">Project</h3>
          <h1 class=" font-mono text-4xl">{{fields['project_title']}}</h1>
      <div class=" font-mono text-gray-500 mt-6">
        
          <prismic-rich-text :field="fields.description"/>
      </div>
      <div class="mt-6">

      
      <a class=" font-mono underline " :href="fields.github_link">View on Github</a>
      </div>
      </div>
      

      <section class=" mt-8 w-full font-mono prose-sm lg:prose-md">
        <content-loader v-if="!ready">
          <rect x="0" y="13" rx="4" ry="4" width="400" height="9" />
    <rect x="0" y="29" rx="4" ry="4" width="100" height="8" />
    <rect x="0" y="50" rx="4" ry="4" width="400" height="10" />
    <rect x="0" y="65" rx="4" ry="4" width="400" height="10" />
    <rect x="0" y="79" rx="4" ry="4" width="100" height="10" />
    <rect x="0" y="99" rx="5" ry="5" width="400" height="200" />
        </content-loader>
          <prismic-rich-text :field="fields.introduction"/>
      </section>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader'
export default {
  components: {
    ContentLoader
  },
  data() {
    return {
      fields: {
        project_title: "",
        github_link: "",
        introduction: [],
        description: []
      },
      ready: false,
      couldNotFound: false
    };
  },
  methods: {
    getContent() {
      let contentId = this.$route.query.id;
      this.$prismic.client.getByID(contentId).then((document) => {
        if (typeof document != "undefined") {
            
            this.fields.description = document.data.description
        this.fields.project_title = document.data['project_title'][0].text
        this.fields.github_link = document.data['github_link'].url
        this.fields.introduction = document.data.introduction
        this.ready = true
        } else {
            this.couldNotFound = true
        }
        
      });
    },
  },
  created() {
    this.getContent();
  },
};
</script>

